<template>
  <div>
    <section class="hero is-fullheight">
      <!-- Hero header: will stick at the top -->
      <div class="hero-head">
        <header class="nav">
          <div class="container">
            <div class="nav-left">
              <gene-list :options="genes" @select="onOptionSelect"></gene-list>
            </div>
            <span class="nav-toggle">
              <span></span>
              <span></span>
              <span></span>
            </span>
            <div class="nav-right nav-menu">
              <a class="nav-item is-active">
                Home
              </a>
            </div>
          </div>
        </header>
      </div>

      <!-- Hero content: will be in the middle -->
      <div class="hero-body has-text-left">
        <section>
          <div class="container is-fluid">
            <div class="heading">
              <h1 class="title">Gene Name: {{ geneName }}</h1>
              <label class="label">NP#</label>
              <p class="control">
              <span class="select">
                <select @change="setNP">
                  <option disabled value="">Please select one</option>
                  <option v-for="i_np in np">{{ i_np }}</option>
                </select>
              </span>
              </p>
            </div>
            <div style="padding: 0% 10% 0% 10%">
              <!-- <p> -->
              <!-- <span v-for="mod in variants" :class="[mod.classification]">{{ mod.text }}<div v-if="mod.classification === 'modification'" v-html="mod.tooltip" class="tooltiptext"></div></span> -->
              <!-- </p> -->
              <hr>
              <p class="sequence-parent">
              <span v-for="mod in allSeq" :class="[mod.classification]"><span v-if="mod.id !== ''" :id="[mod.id]" @mouseover="overSequence(mod.id)" @mouseout="overID=-1; searchQuery=-1">{{ mod.sequence }}</span><span v-else>{{ mod.sequence }}</span><div v-if="mod.classification === 'modification'" v-html="mod.tooltip" class="tooltiptext"></div></span>
              <!-- <span v-for="piece in allSeq" :style="{color: piece.col}">{{ piece.text }}</span> -->
              </p>
            </div>
            <pep-table
                                           :data="selected.variants"
                                           :columns="headers"
                                           :filterKey="searchQuery"></pep-table>
          </div>
        </section>
      </div>
    </section>
  </div>
</template>

<script>
import _ from 'lodash'
const zlib = require('zlib')
import GeneList from './gene-list/gene-list.vue'
import PepTable from './pep-table/pep-table.vue'
export default {
  name: 'hello',
  data () {
    return {
      all: [{'': -1, SEQ: ['asd'], NP_Counts: 1, Gene_Name: 'aaa001'}],
      selected: {'': -1, seq: [], nu: '', variants: {}},
      sequences: [],
      geneName: 'AATK',
      genes: [],
      fasta_seq: '',
      np: [],
      headers: ['Peptide', 'Human Unique', 'Gene Unique', 'Isoform Unique', 'Contains Variant'],
      searchQuery: -1,
      overID: -1
    }
  },
  created () {
    var oReq = new XMLHttpRequest()

    // oReq.addEventListener('progress', updateProgress)
    // oReq.addEventListener('load', transferComplete.call(this))
    // oReq.addEventListener('error', transferFailed)
    // oReq.addEventListener('abort', transferCanceled)

    oReq.open('get', './static/AATK_Gene_variants.json.gz.txt', true)
    // oReq.open('get', '/static/Hu_Output_Ver7.txt', true)
    oReq.onreadystatechange = (aEvt) => {
      if (oReq.readyState === XMLHttpRequest.DONE) {
        const buffer = Buffer.from(oReq.responseText, 'base64')
        zlib.unzip(buffer, (err, buffer) => {
          if (!err) {
            var data = JSON.parse(buffer.toString())
            this.all = data
            this.genes = _.keys(this.all)
            this.np = _.keys(this.all['AATK'])
            this.selected = this.all['AATK'][this.np[0]]
            this.selected.variants = this.all['AATK'][this.np[0]]['peptides']
            // this.headers = _.keys(this.selected.variants[0])
            this.fasta_seq = this.selected.seq
            this.sequences = this.selected.nu
          } else {
            console.log(err)
          }
        })
      }
    }
    oReq.onprogress = function (event) {
      event.loaded
      event.total
      console.log(event.loaded / event.total)
    }
    oReq.send()
  },
  components: {
    GeneList,
    PepTable
  },
  methods: {
    onOptionSelect (option) {
      console.log('Selected option:', option)
      this.geneName = option
      this.np = _.keys(this.all[option])
      this.selected = this.all[this.geneName][this.np[0]]
      this.selected.variants = this.selected['peptides']
      this.fasta_seq = this.selected.seq
      this.sequences = this.selected.nu
    },
    setNP (NP) {
      this.selected = this.all[this.geneName][NP.target.value]
      this.selected.variants = this.selected['peptides']
      this.fasta_seq = this.selected.seq
      this.sequences = this.selected.nu
    },
    resetSS () {
      this.fasta_seq = this.selected.FASTA_SEQ
    },
    overSequence (id) {
      this.searchQuery = id
      this.overID = id
    }

  },
  computed: {
    allSeq () {
      return _.map(this.selected.seq.slice().reverse(), entry => {
        let classification = 'normal'
        let id = ''
        if (entry.t === 'v') {
          classification = 'modification'
        } else if (entry.t === 'p') {
          if (entry.id === this.overID || this.overID === -1) {
            classification = 'peptide col' + entry.o
          } else {
            classification = 'peptide bold'
          }
          id = entry.id
        } else {
          classification = 'normal'
        }
        let tt = '<table><tr><th>protein_ref</th><th>protein_pos</th><th>protein_alt</th></tr>'
        _.forEach(entry.i, r => { tt += '<tr><td>' + r.r + '</td><td>' + r.p + '</td><td>' + r.a + '</td></tr>' })
        tt += '</table>'
        return {'sequence': entry.s, 'classification': classification, 'tooltip': tt, 'id': id}
      })
    },
    variants () {
      var positions = _.reduce(this.selected.variants, (r, v, k) => {
        (r[v.protein_pos] || (r[v.protein_pos] = {protein_pos: v.protein_pos, table: '<table class="table is-bordered is-striped is-narrow"><thead><tr><th>protein_ref</th><th>protein_pos</th><th>protein_alt</th></tr></thead>'}))
        r[v.protein_pos].table += '<tr><td>' + v.protein_ref + '</td><td>' + v.protein_pos + '</td><td>' + v.protein_alt + '</td></tr>'
        return r
      }, {})
      positions = Object.values(positions).sort((a, b) => a.protein_pos - b.protein_pos)
      var res = []
      var tempSeq = this.fasta_seq
      _.forEachRight(positions, e => {
        const s = e.protein_pos
        res.push({text: tempSeq.slice(s), classification: 'normal'})
        res.push({
          text: tempSeq.slice(s - 1, s),
          tooltip: e.table + '</table>',
          classification: 'modification'
        })
        tempSeq = tempSeq.slice(0, s - 1)
      })
      res.push({classification: 'normal', text: tempSeq})
      return res.reverse()
    }
  }
}
</script>

