export default {
  name: 'pep-table',
  props: {
    data: {
      type: Object,
      required: true
    },
    columns: {
      type: Array,
      required: true
    },
    filterKey: Number
  },
  data () {
    var sortOrders = {}
    this.columns.forEach(function (key) {
      sortOrders[key] = 1
    })
    console.log('start', Object.values(sortOrders), sortOrders)
    return {
      sortKey: '',
      sortOrders: sortOrders
    }
  },
  computed: {
    filteredData: function () {
      var sortKey = this.sortKey
      var filterKey = this.filterKey
      var order = this.sortOrders[sortKey] || 1
      var data = this.data
      console.log('fk', filterKey)
      if (filterKey !== -1) {
        let temp = Object.entries(data).filter(function (row) {
          return row[1].id === filterKey
        })
        data = {}
        data[temp[0][0]] = temp[0][1]
      }
      if (sortKey) {
        data = data.slice().sort(function (a, b) {
          a = a[sortKey]
          b = b[sortKey]
          return (a === b ? 0 : a > b ? 1 : -1) * order
        })
      }
      console.log('Data', data)
      return data
    }
  },
  filters: {
    capitalize: function (str) {
      return str.charAt(0).toUpperCase() + str.slice(1)
    }
  },
  methods: {
    sortBy: function (key) {
      this.sortKey = key
      this.sortOrders[key] = this.sortOrders[key] * -1
    },
    highlightPeptide: function (event) {
      // `this` inside methods points to the Vue instance
      console.log(event)
      this.highlightID = 0
      // console.log('Hello ' + event.target.innerText + '!')
      // `event` is the native DOM event
    }
  }
}



